import { Component, OnInit, ViewChild } from '@angular/core';

import {ModalComponent} from '../../../../shared-components/modal/modal.component';

import {miniaturesPackages} from '../../../../services/mock-data';

@Component({
  selector: 'app-manage-miniatures',
  templateUrl: './manage-miniatures.component.html',
  styleUrls: ['./manage-miniatures.component.scss']
})
export class ManageMiniaturesComponent implements OnInit {

  public miniaturesPackages;
  public selectedPackage;
  public selectedMiniature;

  @ViewChild(ModalComponent) modal: ModalComponent;

  constructor() {
    this.miniaturesPackages = miniaturesPackages;
  }
   
  ngOnInit(){
    this.selectedPackage = this.miniaturesPackages[0];
  }

  onPackageClick(pack){
    this.selectedPackage = pack;
    this.selectedMiniature = pack.models[0];
    this.modal.openModal();
  }

  onMiniatureClick(miniature){
    this.selectedMiniature = miniature;
  }
}
