import { Component, OnInit } from '@angular/core';

import { Character, CharacterService, Package, PackageService, GridCharacter } from '../../../../shared-components';

@Component({
  selector: 'app-build-encounter',
  templateUrl: './build-encounter.component.html',
  styleUrls: ['./build-encounter.component.scss']
})
export class BuildEncounterComponent implements OnInit {
  gridWidth = { number: 20 };

  packages: Package[];
  selectedCharacter: Character;
  templateCharacters: Character[];
  characters: GridCharacter[] = [];
  hoveredCharacterIndex = { number: -1 };

  newCharacterModel: GridCharacter = new GridCharacter(
    '0',
    'New Character',
    0,
    0,
    0,
    false,
    { x: 0, y: 0 },
    { x: 1, y: 1 },
    0
  );

  constructor(private characterService: CharacterService, private packageService: PackageService) {}

  ngOnInit() {
    this.getPackages();
  }

  getPackages() {
    this.packageService.get().subscribe(result => {
      this.packages = result['data'];
    });
  }

  getCharacters(packageId: string) {
    this.characterService.getByPackageId(packageId).subscribe(result => {
      this.templateCharacters = result['data'];
    });
  }

  changeSelectedCharacterTemplate(characterId) {
    let char = this.templateCharacters.find(ch => ch.id === characterId);
    this.newCharacterModel.initiativeModifier = char.initiativeModifier;
    this.newCharacterModel.name = char.name;
    this.newCharacterModel.armorClass = char.armorClass;
    this.newCharacterModel.hitPoints = char.hitPoints;
  }

  addAllFromCurrentPackage() {
    for (let char of this.templateCharacters) {
      this.addCustomCharacter(char);
    }
  }

  addCustomCharacter(character) {
    var gridCharacter = new GridCharacter(
      '0',
      character.name,
      character.initiativeModifier,
      character.armorClass,
      character.hitPoints,
      false,
      { x: 0, y: 0 },
      { x: 1, y: 1 },
      0
    );
    this.characters.push(gridCharacter);
  }

  addCharacter() {
    var gridCharacter = new GridCharacter(
      '0',
      this.selectedCharacter.name,
      this.selectedCharacter.initiativeModifier,
      this.selectedCharacter.armorClass,
      0,
      false,
      { x: 0, y: 0 },
      { x: 1, y: 1 },
      0
    );
    this.characters.push(gridCharacter);
  }

  createNewCharacter() {
    this.newCharacterModel.currentHitPoints = this.newCharacterModel.hitPoints;
    this.characters.push(JSON.parse(JSON.stringify(this.newCharacterModel)));
    //this.characters.push(this.newCharacterModel);??
    console.log(this.characters);
  }

  deleteCharacter(character: GridCharacter) {
    let index = this.characters.indexOf(character);
    this.characters.splice(index, 1);
  }

  hoverOverCharacter(i) {
    this.hoveredCharacterIndex.number = i;
  }

  hoverOutOfCharacter(i) {
    this.hoveredCharacterIndex.number = -1;
  }
}
