import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders, HttpErrorResponse, HttpParams } from '@angular/common/http';

import { Observable, throwError } from 'rxjs';
import { catchError, tap } from 'rxjs/operators';
import { SERVER_PATH } from '../../services/app.constants';


@Injectable({
  providedIn: 'root'
})
export class BaseService<T> {
  public baseUrl: string;

  constructor(private httpClient: HttpClient) {
    this.baseUrl = SERVER_PATH;
  }

  extractData(res: Response) {
    const body = res.json();
    return body || {};
  }

  getTokenHeader() {
    let token = window.localStorage.getItem('token');
    return new HttpHeaders({ authorization: token });
  }

  get(url: string, searchParams?: any, responseType?: any): Observable<any> {
    return this.httpClient
      .get(this.baseUrl + url,
        {
          headers: this.getTokenHeader(),
          withCredentials: true, params: searchParams,
          responseType: responseType
        })
      .pipe(
        tap((result) => console.log('result-->', result)),
        catchError(this.handleError)
      );
  }

  //this is not working due to CORS policy. Needs investigation
  patch(url: string, body: any, params: HttpParams): Observable<any> {

    return this.httpClient.patch(this.baseUrl + url, body, {
      headers: this.getTokenHeader(),
      params, withCredentials: true
    })
      .pipe(
        tap((result) => console.log('result-->', result)),
        catchError(this.handleError)
      );
  }

  post(url: string, body: any, params: HttpParams): Observable<any> {

    return this.httpClient.post(this.baseUrl + url, body, {
      headers: this.getTokenHeader(),
      params, withCredentials: true
    })
      .pipe(
        tap((result) => console.log('result-->', result)),
        catchError(this.handleError)
      );
  }

  put(url: string, body: any, params: HttpParams): Observable<any> {
    return this.httpClient.put(this.baseUrl + url, body, {
      headers: this.getTokenHeader(),
      params, withCredentials: true
    })
      .pipe(
        tap((result) => console.log('result-->', result)),
        catchError(this.handleError)
      );
  }

  delete(url: string, params: HttpParams): Observable<any> {

    return this.httpClient.delete(this.baseUrl + url, { params, withCredentials: true })
      .pipe(
        tap((result) => console.log('result-->', result)),
        catchError(this.handleError)
      );
  }

  private handleError(error: HttpErrorResponse) {
    if (error.error instanceof ErrorEvent) {
      // A client-side or network error occurred. Handle it accordingly.
      console.error('An error occurred:', error.error.message);
    } else {
      // The backend returned an unsuccessful response code.
      // The response body may contain clues as to what went wrong,
      console.error(
        `Backend returned code ${error.status}, ` +
        `body was: ${error.error}`);
    }
    // return an observable with a user-facing error message
    return throwError(
      'Something bad happened; please try again later.' + error.message);
  };
}
