import { Component, OnInit, Input, ViewChild, ElementRef, HostListener, Output, EventEmitter } from '@angular/core';
import { GridCharacter } from '../../../../shared-components/models/grid-character.model';

@Component({
  selector: 'grid',
  templateUrl: './grid.component.html',
  styleUrls: ['./grid.component.scss']
})
export class GridComponent implements OnInit {
  @Input()
  gridWidth = { number: 20 };

  @Input()
  elements: GridCharacter[];

  @ViewChild('gridWrapper')
  gridWrapper: ElementRef;

  @Output()
  elementHoveredIn: EventEmitter<number> = new EventEmitter();

  @Output()
  elementHoveredOut: EventEmitter<number> = new EventEmitter();

  cellWidth: number;

  isDraggingCharacter = false;
  selectedCharacterIndex: number = -1;
  hoveredCharacterIndex: number = -1;

  constructor() {}

  ngOnInit() {
    var width = this.gridWrapper.nativeElement.offsetWidth;
    this.cellWidth = width / this.gridWidth.number;
  }

  @HostListener('window:resize', ['$event'])
  onResize(event: any) {
    var width = this.gridWrapper.nativeElement.offsetWidth;
    this.cellWidth = width / this.gridWidth.number;
  }

  arrayOfSize(w: number, h: number): any[] {
    return Array(w * h).fill(0);
  }

  switchDragCharacterState(event, i) {
    if (!this.isDraggingCharacter) {
      this.isDraggingCharacter = true;
      this.selectedCharacterIndex = i;
    } else {
      this.isDraggingCharacter = false;
      this.selectedCharacterIndex = -1;
    }
  }

  gridCellHovered(event, i) {
    if (!this.isDraggingCharacter) return;
    const y = Math.floor(i / this.gridWidth.number);
    const x = i % this.gridWidth.number;
    this.elements[this.selectedCharacterIndex].lowerRightCoordinates.x =
      this.elements[this.selectedCharacterIndex].lowerRightCoordinates.x -
      this.elements[this.selectedCharacterIndex].upperLeftCoordinates.x +
      x;
    this.elements[this.selectedCharacterIndex].lowerRightCoordinates.y =
      this.elements[this.selectedCharacterIndex].lowerRightCoordinates.y -
      this.elements[this.selectedCharacterIndex].upperLeftCoordinates.y +
      y;
    this.elements[this.selectedCharacterIndex].upperLeftCoordinates.x = x;
    this.elements[this.selectedCharacterIndex].upperLeftCoordinates.y = y;
  }

  hoverOverCharacter(event, i) {
    this.hoveredCharacterIndex = i;
    this.elementHoveredIn.emit(i);
  }
  hoverOutOfCharacter(event, i) {
    this.hoveredCharacterIndex = -1;
    this.elementHoveredOut.emit(i);
  }
}
