import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Routes, RouterModule } from '@angular/router';
import { FormsModule } from '@angular/forms';

import { SharedModule } from '../../shared-components/shared-module/shared-module.module';
import { ModalComponent } from '../../shared-components/modal/modal.component';

import { BuildEncounterComponent } from './components/build-encounter/build-encounter.component';
import { RoomComponent } from './components/room/room.component';
import { MainComponent } from './main.component';
import { GridElementList } from './components/shared-components/grid-element-list.component';
import { GridComponent } from './components/shared-components/grid.component';
import { Auth, AuthGuard } from '../../services/auth.service';
import { ManageMiniaturesComponent } from './components/manage-miniatures/manage-miniatures.component';
import { PackageComponent } from './components/package/package.component';
import { PackageDetailsComponent } from './components/package/package-details/package-details.component';
import { CharactersComponent } from './components/characters/characters.component';
import { CharacterDetailsComponent } from './components/characters/character-details/character-details.component';

export const encounterRoutes: Routes = [
  {
    path: 'main',
    component: MainComponent,
    canActivate: [AuthGuard],
    children: [
      {
        path: 'build-encounter',
        component: BuildEncounterComponent
      },
      {
        path: 'room',
        component: RoomComponent
      },
      {
        path: 'manage-miniatures',
        component: ManageMiniaturesComponent
      },
      {
        path: 'packages',
        component: PackageComponent
      },
      {
        path: 'characters',
        component: CharactersComponent
      }
    ]
  }
];

@NgModule({
  imports: [CommonModule, RouterModule.forChild(encounterRoutes), SharedModule, FormsModule],
  providers: [Auth, AuthGuard],
  declarations: [
    BuildEncounterComponent,
    RoomComponent,
    MainComponent,
    ManageMiniaturesComponent,
    GridElementList,
    GridComponent,
    PackageComponent,
    PackageDetailsComponent,
    CharactersComponent,
    CharacterDetailsComponent
  ]
})
export class EncounterModule {}
