import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import {GridCharacter} from '../../../../shared-components/models/grid-character.model';

@Component({
  selector: 'grid-element-list',
  templateUrl: './grid-element-list.component.html',
  styleUrls: ['./grid-element-list.component.scss']
})
export class GridElementList implements OnInit {
  @Input()
  elements: GridCharacter[];

  @Input()
  hoveredElementIndex = { number: -1 };

  @Output()
  elementDeleted: EventEmitter<any> = new EventEmitter();

  constructor() {}

  ngOnInit() {}

  rollInitiative() {
    for (let element of this.elements) {
      const d20 = Math.floor(Math.random() * 20) + 1;
      element.initiative = d20 + element.initiativeModifier;
    }
    this.sort();
  }

  sort() {
    this.elements.sort((a, b) => (a.initiative < b.initiative ? 1 : -1));
  }
}
