import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

import { Package, PackageService } from '../../../../../shared-components';

@Component({
  selector: 'app-package-details',
  templateUrl: './package-details.component.html',
  styleUrls: ['./package-details.component.scss']
})
export class PackageDetailsComponent implements OnInit {
  @Input() selectedPackage: Package;

  @Output() detailsClose = new EventEmitter();

  constructor(private packageService: PackageService) { }

  ngOnInit() {
  }

  save() {
    this.packageService
      .add(this.selectedPackage)
      .subscribe(
        data => { this.close(); },
        err => {}
      );
  };

  update() {
    this.packageService
      .update(this.selectedPackage)
      .subscribe(
        data => { this.close(); },
        err => {}
      );
  };

  delete() {
    this.packageService
      .delete(this.selectedPackage.id)
      .subscribe(
        data => { this.close(); },
        err => {}
      );
  };

  close() {
    this.detailsClose.emit();
  }

}
