import { Component, OnInit } from '@angular/core';
import { Package, PackageService } from '../../../../shared-components';

@Component({
  selector: 'app-package',
  templateUrl: './package.component.html',
  styleUrls: ['./package.component.scss']
})
export class PackageComponent implements OnInit {

  selectedPackage: Package;
  packages: Package[];

  constructor(private packageService: PackageService) { }

  ngOnInit() {
    this.getPackages();
  }

  getPackages() {
    this.packageService.get().subscribe(result => {
      this.packages = result["data"];
    });
  }

  get isItemSelected() {
    return this.selectedPackage != null;
  }

  rowSelected(item: Package) {
    this.selectedPackage = item;
  }

  create() {
    this.selectedPackage = new Package();
  }

  handleDetailsClosed() {
    this.selectedPackage = null;
    this.getPackages();
  }

}
