import { Component, OnInit, Input, Output, EventEmitter  } from '@angular/core';

import {
  Character, CharacterService,
  Package, PackageService 
} from '../../../../../shared-components';

@Component({
  selector: 'app-character-details',
  templateUrl: './character-details.component.html',
  styleUrls: ['./character-details.component.scss']
})
export class CharacterDetailsComponent implements OnInit {
  @Input() selectedCharacter: Character;

  packages: Package[];
  @Output() detailsClose = new EventEmitter();

  constructor(private characterService: CharacterService,
    private packageService: PackageService) { }

  ngOnInit() {
    this.getPackages();
  }

  getPackages() {
    this.packageService.get().subscribe(result => {
      this.packages = result["data"];
    });
  }

  save() {
    this.characterService
      .add(this.selectedCharacter)
      .subscribe(
        data => { this.close(); },
        err => { }
      );
  };

  update() {
    this.characterService
      .update(this.selectedCharacter)
      .subscribe(
        data => { this.close(); },
        err => { }
      );
  };

  delete() {
    this.characterService
      .delete(this.selectedCharacter.id)
      .subscribe(
        data => { this.close(); },
        err => { }
      );
  };

  close() {
    this.detailsClose.emit();
  }
}
