import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { CanActivate, Router, CanLoad } from '@angular/router';

import { Observable, Observer, Subject } from 'rxjs/Rx';
import { SERVER_PATH } from './app.constants';

@Injectable()
export class Auth {
    public sizesAfterLoginObservable;

    private User;
    private initializingObserver;

    constructor(private HttpClient: HttpClient, private Router: Router) {
        this.getLoginStatus()
            .finally(() => {
                if (this.initializingObserver !== undefined) {
                    this.initializingObserver.next(this.User);
                    this.initializingObserver.complete();
                }
            })
            .subscribe(
                (user: any) => {
                    this.setUpUser(user, true);
                },
                err => {
                    this.setUpUser(null, false);
                }
            );
    }

    public getLoginStatus() {
        let token = window.localStorage.getItem('token');
        let headers = new HttpHeaders({ authorization: `Bearer ${token}` });
        
        if(token === null) return Observable.create(observer => {
            observer.next(null);
        });
        else return Observable.create(observer => {
            observer.next({"username":"dragos"});
        });
        // return this.HttpClient.get(`${SERVER_PATH}/user/login-status`, { headers });
    }

    getUser() {
        return this.User;
    }

    public hasInitialized() {
        return Observable.create((observer: Observer<any>) => {
            if (this.User !== undefined) {
                observer.next(this.User);
                observer.complete();
            } else {
                this.initializingObserver = observer;
            }
        });
    }

    public logout() {
        this.setUpUser(null, false);
        window.localStorage.removeItem('token');

        this.Router.navigate([''], { replaceUrl: true });
    }

    private setUpUser(user, isLogged = false) {
        this.User = user;
    }
}
@Injectable()
export class AuthResolver {
    constructor(private Auth: Auth) {}

    resolve() {
        return this.Auth.hasInitialized();
    }
}

@Injectable()
export class LoginGuard implements CanActivate {
    constructor(private Auth: Auth, private Router: Router) {}

    canActivate() {
        console.log("Guarding Login", this.Auth.getUser());
        return this.Auth.hasInitialized()
            .map(user => {
                if (user === null) {
                    window.localStorage.removeItem('token');
                    return true;
                } else {
                    throw new Error();
                }
            })
            .catch(() => {
                this.Router.navigate(['/main/build-encounter']);
                return Observable.of(false);
            });
    }
}

@Injectable()
export class AuthGuard implements CanActivate {
    constructor(private Auth: Auth, private Router: Router) {}

    canActivate() {
        console.log("Guarding Auth");
        return this.Auth.hasInitialized()
            .map(user => {
                if (user !== null) {
                    return true;
                } else {
                    window.localStorage.removeItem('token');
                    throw new Error();
                }
            })
            .catch(() => {
                this.Router.navigate(['']);
                window.localStorage.removeItem('token');
                return Observable.of(false);
            });
    }
}

import { HttpInterceptor } from '@angular/common/http';

@Injectable()
export class AuthInterceptor implements HttpInterceptor {
    intercept(req, next) {
        const authToken = window.localStorage.getItem('token');

        if (authToken) {
            console.log(`Sending request with header ${authToken}`);
            let request = req.clone({
                setHeaders: {
                    authorization: `Bearer ${authToken}`
                }
            });
            return next.handle(request);
        }
        return next.handle(req.clone());
    }
}