import { BrowserModule } from '@angular/platform-browser';
import { NgModule} from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { HTTP_INTERCEPTORS, HttpClientModule } from '../../node_modules/@angular/common/http';

import { SocialLoginModule, AuthServiceConfig, GoogleLoginProvider, FacebookLoginProvider} from "angular-6-social-login";

import { AppComponent } from './app.component';
import {LoginModule} from './modules/login/login.module';
import { EncounterModule } from './modules/encounter/encounter.module';

import { Auth, AuthResolver, LoginGuard, AuthGuard, AuthInterceptor } from './services/auth.service';


const appRoutes: Routes = [{
  path:'',
  loadChildren: "./modules/login/login.module#LoginModule"
},{
  path:'main',
  loadChildren: "./modules/encounter/encounter.module#EncounterModule",
  resolve: {
    AuthResolver  
  }
}]

// Configs 
export function getAuthServiceConfigs() {
  let config = new AuthServiceConfig(
      [
        {
          id: FacebookLoginProvider.PROVIDER_ID,
          provider: new FacebookLoginProvider("2267612076802291")
        },
        {
          id: GoogleLoginProvider.PROVIDER_ID,
          provider: new GoogleLoginProvider("1037376100592-9mfcdsrdtu5eve8alnr6di9admq0savu.apps.googleusercontent.com")
        }
      ]
  );
  return config;
}


@NgModule({
  declarations: [
    AppComponent
  ],
  imports: [
    BrowserModule,
    RouterModule.forRoot(appRoutes),
    SocialLoginModule,
    HttpClientModule,
    LoginModule,
    EncounterModule,
  ],
  providers: [
    {
      provide: AuthServiceConfig,
      useFactory: getAuthServiceConfigs,
    },
      Auth,
      AuthResolver,
      {
        provide: HTTP_INTERCEPTORS,
        useClass: AuthInterceptor,
        multi: true
      }
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }
