import { Component, OnInit } from '@angular/core';

import { Character, CharacterService } from '../../../../shared-components';

@Component({
  selector: 'app-characters',
  templateUrl: './characters.component.html',
  styleUrls: ['./characters.component.scss']
})
export class CharactersComponent implements OnInit {

  selectedCharacter: Character;
  characters: Character[];

  constructor(private characterService: CharacterService) { }

  ngOnInit() {
    this.getCharacters();
  }

  getCharacters() {
    this.characterService.get().subscribe(result => {
      this.characters = result["data"];
    });
  }

  get isItemSelected() {
    return this.selectedCharacter != null;
  }

  rowSelected(item: Character) {
    this.selectedCharacter = item;
  }

  create() {
    this.selectedCharacter = new Character();
  }

  handleDetailsClosed() {
    this.selectedCharacter = null;
    this.getCharacters();
  }

}
